<template>
  <div class="app" v-loading="loading">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="70px" class="demo-ruleForm">
        <el-form-item label-position='left' label="账号" prop="username">
            <el-input v-model="ruleForm.username" placeholder="请输入账号" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密码" prop="passwordtrue">
            <el-input type="password" v-model="ruleForm.passwordtrue" placeholder="请输入密码" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="验证码" prop="captcha">
            <div style="width:100%;display:flex;justify-content:space-between;">
              <el-input v-model="ruleForm.captcha" placeholder="请输入验证码" autocomplete="off" style="width:73%;"></el-input>
              <img src="/getCaptcha" id="imgYzm" alt="captcha" @click="editCaptcha" style="width:25%;height:40px;overflow:hidden;border-radius:4px;">
            </div>
        </el-form-item>
        <div style="display:flex;justify-content:center;">
            <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
            <el-button @click="register()">注册</el-button>
            <el-button @click="resetpass()">找回密码</el-button>
        </div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
      var validateusername = (rule, value, callback) => {
        if (value === '') {
            callback(new Error('请输入账号'));
        } else if(!(/^1[3456789]\d{9}$/.test(value))) {
            callback(new Error('请输入正确的账号'));
        }else{
            callback();
        }
    };
    return {
        ruleForm: {
            username:'',
            password: '',
            passwordtrue:'',
            captcha:'',
            createtime:''
        },
        loading:false,
        rules: {
            username:[
                {required: true,validator: validateusername, trigger: 'blur'}
            ],
            passwordtrue: [
                { required: true, message: '密码不能为空', trigger: 'blur'},
            ],
            captcha:[
                { required: true, message: '验证码不能为空', trigger: 'blur'},
            ]
        }
    };
  },
  created(){
    this.editCaptcha()
  },
  methods:{
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            if (valid) {
                console.log(this.ruleForm)
                this.loading=true
                this.ruleForm.createtime=this.$tools.getnowtime(new Date())
                this.ruleForm.password=this.$md5.hex_md5(this.ruleForm.passwordtrue)
                this.ruleForm.passwordtrue=this.$md5.hex_md5(this.ruleForm.passwordtrue)
                this.$axios.post('/loginIn',this.ruleForm).then(res=>{
                    console.log(res)
                    this.loading=false
                    if(res.data.state=='0'){
                        console.log('登录成功')
                        this.$router.push('/index')
                    }else{
                        this.$message({
                            message:res.data.msg,
                            type:'error'
                        })
                        this.editCaptcha()
                    }
                })
            } else {
                console.log('error submit!!');
                return false;
            }
        });
    },
    resetpass(){
      this.$router.push('/forgetpass')
    },
    resetForm(formName) {
        this.$refs[formName].resetFields();
    },
    register(){
      this.$router.push('/register')
    },
    editCaptcha(){
      document.getElementById('imgYzm').src = '/getCaptcha?d='+Math.random()
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.app{
    display: flex;
    justify-content: center;
    margin-top:100px;
}
#app .demo-ruleForm{
    /* width: 50%; */
}
</style>


