function toer(n){
    if(n<10){
        return '0'+n
    }else{
        return n.toString()
    }
}
function getnowtime(date){
    let y=date.getFullYear()
    let m=date.getMonth()+1
    let d=date.getDay()
    let h=date.getHours()
    let M=date.getMinutes()
    let s=date.getSeconds()
    return toer(y)+'-'+toer(m)+'-'+toer(d)+' '+toer(h)+':'+toer(M)+':'+toer(s)
}

export default{
    getnowtime
}



